.ProjectPageBackground {
  margin-top: 70px;
  padding-top: 15px;
  /* min-height: calc(100vh - 123px); */
}

.ProjectPageBackground--container {
  position: fixed;
  width: 100%;
  -webkit-transform: skewY(-12deg);
  transform: skewY(-12deg);
  -webkit-transform-origin: 0 center;
  transform-origin: 0 center;
  background: #f0806f;
  height: 500px;
  left: 0;
  z-index: -1;
}
